import React, { Component } from 'react';
import styled from 'styled-components';
import { RouteList } from './route-list';
import { BidList } from './bid-list';
import { Certs } from './certs';
import {
  StyledMain, Wrapper, Title, StyledDiv, About, WrapperTitle
} from './styled';
import { News } from '../common/news';
import { Reserve } from '../pages/reserve';
import { Title as AboutTitle } from './certs/styled';


const AboutText = '        ()        03.07.2016  238-    ,        . {newLine}   ,      ,  ,    ,      ,    ,    -    ,                 .';

const dataRoute = [
  {
    count: 10,
    name: '  ',
  },
  {
    count: 30,
    name: ' ',
  },
];


export class Main extends Component {
  state = {
    hasError: false,
  }

  static getDerivedStateFromError(e) {
    return {
      hasError: true,
    };
  }

  render() {
    const { hasError } = this.state;
    return hasError ? <Reserve /> : (
      <StyledMain>
        <Wrapper>
          <WrapperTitle>
            <Title>
              <span> </span>
              <strong>     </strong>
            </Title>
            <RouteList data={dataRoute}/>
          </WrapperTitle>
          <BidList />
          <StyledDiv>
            <About>
              <AboutTitle style={{ paddingTop: '10px' }}> </AboutTitle>
              {AboutText.split('{newLine}').map(it => <p style={{ padding: '0 0 5px', textAlign: 'justify' }}>{it}</p>)}
            </About>
            <Certs />
          </StyledDiv>
          <News />
        </Wrapper>
      </StyledMain>
    );
  }
}
